.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.jumbotron {
  background: #00384d !important;
  padding: 2rem !important;
}

.display-name {
  color: #ffffff;
  font-family: 'Georgia, Garamond, "Times New Roman", Times, serif';
}

.address > label {
  color: #ffffff;
}

.fa-address-card-o {
  color: #000;
  background: #fff;
}

.fa-phone, .fa-envelope-o, .fa-linkedin {
  color: #fff;
}

.header-label, .header-label > a {
  color: #fff;
  text-decoration: none;
}

.header-label > a:hover {
  color: #fff;
}

.display-5 {
  color: #000;
}

.year {
  color: #32312f;
}

.position {
  font-weight: 500;
  color: #116f93;
}

.title {
  font-weight: 500;
}

h2 {
  font-family: 'Georgia, Garamond, "Times New Roman", Times, serif';
  color: #0e2024;
}

.section-header {
  color: #fff;
  background: #00384d;
  padding: 0.2rem 0 0.2rem 0.5rem;
}

.img {
  width: 12rem;
  border-radius: 12rem;
}

.header {
  color: #fff;
  background: #467b8f;
  padding: 0.2rem 0 0.2rem 0.5rem;
}

.text-indent {
  text-indent: 2rem;
}
